@media screen and (max-width: 768px) {
  .topnav{
    margin: 2.5rem 1.25rem 2.5rem 2rem;
  }
 
  .topnav a {
    display: none;
  }
  .topnav a.icon {
    float: right;
    display: block;
  }
  .topnav.responsive {
    position: relative;
  }
  .topnav.responsive .icon {
    position: absolute;
    right: 0;
    top: 0;
  }
  .topnav.responsive a {
    float: none;
    display: block;
    text-align: left;
  }
  // a.icon {
  //   color: red;
  // }
  // a.icon:hover {
  //   background-color: transparent;
  // }
  main{
    margin: 0 2rem;
  }
  .hero-section {
    // display: block;
    // box-shadow: unset;
    display: flex;
    flex-direction: column;
    box-shadow: none;
  }
  .hero-section > .btn-signup {
    display: block;
    margin: 32px auto 10px;
    padding: 12px 35px;
  }
  .hero-content {
    background: #fff;
    color: #000;
    // padding: 20px;
    width: 100%;
    padding: 3.25rem 0;
  }
  .hero-content::after {
    content: unset;
  }
  .hero-content .btn-signup {
    display: none;
  }
  // .hero-headline {
  //   font-size: 5rem;
  //   white-space: normal;
  // }
  .hero-headline {
    font-size: 4rem;
    white-space: normal;
    width: 100%;
  }
  .hero-text {
    width: 100%;
    margin-top: 20px;
    font-size: 1.5rem;
  }
  .hero-img img {
    border-radius: 10px;
    margin-top: 20px;
  }
  .copyrights {
    margin-top: 10px;
    text-align: center;
  }
}

@media screen and (max-width: 390px) {
  .header-logo {
    margin-top: 0.7rem;
    margin-left: 0.7rem;
  }
  
  .header-logo img {
    max-height: 1.5rem;
  }
  .hero-headline {
    font-size: 2.1rem;
  }
  .hero-text {
    word-wrap: break-word;
    font-size: 1rem;
  }

  .copyrights {
    margin-top: 0px;
    text-align: center;
    font-size: 0.9rem;
  }
}
